import React, { useState } from 'react';
import { withRouter } from 'react-router-dom';
import AddTaskBtn from '../../components/AddTask/AddTaskBtn';
import PageHeading from '../../components/PageHeading/PageHeading';
import TaskListMessages from '../../components/TaskListMessages/TaskListMessages';
import Button from '../../components/UI/Button/Button';
import { messages } from '../../constants/Texts';
import classes from './TaskList.module.scss';
import listSettings from '../../assets/images/tomato_settings.svg';
import addTask from '../../assets/images/tomato-add.svg';
import tomatoFirst from '../../assets/images/tomato-first.svg';

const checkTaskState = (state) => {

  const properties = { icon: listSettings, text: messages.firstVisit };
  if (state.isFirstVisit) {
    return properties;
  }
  if (!state.tasks.toDo.length && !state.tasks.done.length) {
    properties.icon = tomatoFirst;
    properties.text = messages.firstTask;
    return properties;
  }
  if (!state.tasks.toDo.length) {
    properties.icon = addTask;
    properties.text = messages.noTasks;
    return properties;
  }
  return null;
};

const checkIsFirstVisit = () => {
  const isFirstVisit = sessionStorage.getItem('firstVisit');
  sessionStorage.setItem('firstVisit', 'true');
  return !isFirstVisit;
};

export default withRouter(({ history }) => {
  const [state, setState] = useState({
    isFirstVisit: checkIsFirstVisit(),
    tasks: { toDo: [], done: [] },
  });
  const properties = checkTaskState(state);
  const skip = () => setState({...state, isFirstVisit: false });
  const redirectToSettings = () => history.push('/settings');
  return (
    <div className={classes.TaskList}>
      <div className={classes.AddTaskWrapper}>
        <PageHeading title="Daily Task List" />
        <AddTaskBtn />
      </div>
      {properties
        ? (
          <TaskListMessages
            pict={properties.icon}
            text={properties.text}
          />
        )
        : null}
      {state.isFirstVisit
        ? (
          <div className={classes.ButtonWrapper}>
            <Button clickHandler={skip} color="Blue">Skip</Button>
            <Button clickHandler={redirectToSettings} color="Green">Go to Settings</Button>
          </div>
        )
        : null}
    </div>
  );
});
